nav{position: fixed;top: 0;width: 100%;background-color: rgba(255,255,255,0.5);height: 4vw;display: flex;align-items: center;padding: 0 15px;}
nav a{font-size:1.5vw;text-decoration: none;color:#FFF;margin:0 1vw;letter-spacing:0.075em;font-family:"news-gothic-std",sans-serif;font-weight:400;}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #123456;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
a{
  color:#FFF;
  font-size:15px;
}
